import Cookies from 'js-cookie'
import { objEqual } from './common'
const TokenKey = 'blog_token'

export function getToken () {
  return Cookies.get(TokenKey)
}

export function setToken (token) {
  return Cookies.set(TokenKey, token)
}

export function removeToken () {
  return Cookies.remove(TokenKey)
}
/**
 * @description maxLen
 * @param arr Array 
 * @param obj   Object
 */
export const objAddToArr = (arr, obj) => {
  arr.forEach((val, ind) => {
    if (objEqual(val, obj)) {
      arr.splice(ind, 1)
    }
  })
  // 
  if (_isDefaultPage(arr, obj)) return 0
  // maxLen
  if (_isExtendedMaxLen(arr, 10)) {
    arr.splice(2, 1)
  }
  arr.push(obj)
  return arr.length - 1
}
// 
const _isDefaultPage = (arr, obj) => {
  if (arr[0].path == obj.path) {
    return true
  }
  return false
}
// 
const _isExtendedMaxLen = (arr, maxLen) => {
  if (arr.length >= maxLen) {
    return true
  }
  return false
}