import router from './router'
import store from './store'
import { Message } from 'element-ui'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css' // Progress 
import { getToken } from '@/utils/modules/auth' // get token from cookie
import getPageTitle from '@/utils/modules/getPageTitle'

NProgress.configure({
  showSpinner: false // NProgress Configuration,
})

const whiteList = ['/login'] // 

// 
router.beforeEach(async (to, from, next) => {

  NProgress.start()
  // 
  document.title = getPageTitle(to.meta.title)
  // cookietoken
  const hasToken = getToken()
  console.log(hasToken)
  // token,
  if (hasToken) {
    if (to.path === '/login') {
      next({
        path: '/'
      })
      NProgress.done()
    } else {
      // store
      const hasRoles = store.getters.roles && store.getters.roles.length > 0
      if (hasRoles) {
        // roles
        next()
      } else {
        try {
          // storerolesroles
          const { roles } = await store.dispatch('user/getInfo')

          // 
          const accessRoutes = await store.dispatch('permission/generateRoutes', roles)
          // 
          router.addRoutes(accessRoutes)

          // hack addRoutes
          // set the replace: true, so the navigation will not leave a history record
          next({
            ...to,
            replace: true
          })
        } catch (error) {
          await store.dispatch('user/resetToken') //token
          Message.error(error || 'Has Error')
          next(`/login?redirect=${to.path}`) //
          NProgress.done()
        }
      }
    }
  } else {
    /* token,*/
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next(`/login?redirect=${to.path}`)
      NProgress.done()
    }
  }
})

router.afterEach(() => {
  NProgress.done()
})